import React from 'react';
import { useHistory } from 'react-router';
import CenterDiv from '../styled/CenterDiv';
import StyledMenu from '../styled/StyledMenu';
import * as ProjectsData from '../json/projects.json';
import * as ContactData from '../json/contactinfo.json';

const MainPage = (): JSX.Element => {
  document.title = 'Burhan Topaloglu: Game/Web/App Developer';

  return (
    <>
      <header>
        <IntroDiv />
      </header>
      <body>
        <ProjectDiv />
      </body>
      <footer>
        <div id='about'>
          <CenterDiv>
            <h1>About me</h1>
            <p>Hey! I am Burhan Topaloglu.</p>
          </CenterDiv>
        </div>
        <div id='contact'>
          <ContactsDiv />
        </div>
      </footer>
      <Menu />
    </>
  );
}

const IntroDiv = (): JSX.Element => {
  return (
    <CenterDiv>
      <h1>Burhan Topaloglu</h1>
      <h4>Game and Web Developer</h4>
      <p>Welcome to my portfolio!</p>
    </CenterDiv>);
};

const ProjectDiv = (): JSX.Element => {

  if (ProjectsData.Projects.length === 0) return <CenterDiv>No Projects available</CenterDiv>;

  return <CenterDiv>
    {
      ProjectsData.Projects.map((project, _index) => {
        return (
          <React.Fragment key={_index}>
            <div id={project.tag}>
              <h2>{project.title}</h2><br />
              {project.img !== '' ? <img src={`/media/${project.img}`} width='500' height='300' alt={project.img}></img> : <p></p>}
              <h3>About this project:</h3> <br />
              <br />
            </div>
          </React.Fragment>
        );
      })
    }
  </CenterDiv>

};

const ContactsDiv = (): JSX.Element => {
  if (ContactData.Contacts.length === 0) return <CenterDiv>No Contact info available</CenterDiv>;
  const contacts = ContactData.Contacts;

  return (
    <CenterDiv>
      <h1>Contact info</h1>
      {
        contacts.map((site, _index) => {
          return (
            <div id={site.tag} key={_index}>
              <h3>{site.name}</h3>
              <h4><button onClick={() => (window.open(site.link))}>{site.linkText}</button></h4>
            </div>);
        })
      }
    </CenterDiv>
  );
};

const Menu = (): JSX.Element => {
  const history = useHistory();

  if (ProjectsData.Projects.length === 0) return <CenterDiv>No Projects available</CenterDiv>;

  return (
    <StyledMenu id="menu">
      <h2>
        {'Navigation '}
        <button onClick={() => {
          window.location.hash = 'contact';
          history.push('')
        }}>Contact info</button>
      </h2>
      <ul>
        <li><button onClick={() => {
          window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
          });
          history.push('');
        }}>Top</button></li>
        {
          ProjectsData.Projects.map((project) => {
            return (<li><button onClick={() => {
              window.location.hash = project.tag;
              history.push('');
            }}>{project.title}</button></li>);
          })
        }
        <li><button onClick={() => {
          window.location.hash = 'about';
          history.push('');
        }}>About me</button></li>
      </ul>
    </StyledMenu>
  );
};

export default MainPage;